.navbar {
  background-color: #1f1f1f;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  font-weight: 600;
  top: 0;
  z-index: 20;
  border-bottom: 1px solid rgb(41, 41, 41);
  box-sizing: border-box;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
}
.nav-container .website-name {
  display: inline-block;
  color: #fff;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  flex-grow: 1;
}
.nav-container .nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
}
.nav-container .nav-menu .nav-item {
  line-height: 40px;
  margin-right: 25px;
}
.nav-container .nav-menu .nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}
.nav-container .nav-menu .nav-item:hover:after {
  width: 100%;
  background: #fff;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  height: 100%;
  border-bottom: 3px solid transparent;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-container {
    justify-content: flex-start;
  }
  .nav-container .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1px solid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
    overflow-x: hidden;
    padding: 0;
  }
  .nav-container .nav-menu.active {
    background: #000;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-container .nav-menu .nav-item {
    margin-right: inherit;
  }
  .nav-container .website-name {
    flex-grow: inherit;
    width: 40%;
  }
  .nav-item .active {
    color: #8d8d8d;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
    box-sizing: border-box;
  }
  .nav-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-50%, -50%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #ffffff;
    width: 50px;
    height: 50px;
    margin-right: inherit;
    border: 1px solid #ffffff;
    box-sizing: border-box;
  }
}/*# sourceMappingURL=navbar.css.map */